import './_item';

import React from 'react';
import BaseComponent from '../../../base-component';

let ThsrcItem = ({props}) => (
    <div className="item__inner">
        <div className="item__heading">
                <p></p>
                <p>{props.trainType} </p>
        </div>
        <div className="item__body">
            <p className="item__time">
                <small className="item__small"></small>
                <time dateTime={props.startTime}>{props.startTime}</time>
            </p>
        </div>
    </div>
);

let TwtrafficItem = ({props}) => (
    <div className="item__inner">
        <div className="item__heading">
                <p>{props.trainType}</p>
                <p>: {props.router} </p>
        </div>
        <div className="item__body">
            <p className="item__time">
                <small className="item__small"></small>
                <time dateTime={props.startTime}>{props.startTime}</time>
            </p>
        </div>
        <div className="item__footer">
            <p>
                {(props.state === '0' || props.state === '') ? '' : `${props.state}`}
            </p>
        </div>
    </div>
);

class Item extends BaseComponent {
    constructor(props) {
        super(props);
        this._bind();
    }
    render() {
        let contentHtml = this.props.type === 'twtraffic' ? <TwtrafficItem  props={this.props} /> : <ThsrcItem props={this.props} />;

        return (
            <li className="item" >
                {contentHtml}
            </li>
        );
    }
}

Item.propTypes = {
    type     : React.PropTypes.string,
    trainType: React.PropTypes.string,
    startTime: React.PropTypes.string,
    router   : React.PropTypes.string,
    state    : React.PropTypes.string
};
Item.defaultProps = {
    type     : 'twtraffic',
    trainType: '',
    startTime: '',
    router   : '',
    state    : ''

};

export default Item;



